<template>
  <div class="magazine">
    <div v-for="(item,index) in arr" :key="index">
      <div class="box">
        <img :src="item.url" :name="item.name" @click="pu" />
        <p>{{item.name}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      arr: [],
      newProject: {
        name: "",
        url: ""
      }
    };
  },
  created() {
    var AV = require("leancloud-storage");
    var query = new AV.Query("_File");
    query.equalTo("mime_type", "image/jpeg");

    query.find().then(
      result => {
        for (var i = 0; i < result.length; i++) {
          this.arr.push({
            name: result[i].attributes.name.slice(0, -4),
            url: result[i].attributes.url
          });
        }
      },
      error => {
        console.log(JSON.stringify(error));
      }
    );
  },
  methods: {
    pu(e) {
      if (this.$store.state.userConfirm) {
        this.newProject = {
          name: e.target.name,
          url: e.target.src
        };
        this.updateProject();
        if (this.$store.state.updateProject) {
          this.updateSuccess();
        } else {
          this.updateError();
        }
      } else {
        this.noLog();
      }
    },
    updateProject() {
      this.$store.commit("updateProject", this.newProject);
    },
    updateSuccess() {
      const h = this.$createElement;
      this.$notify({
        title: "",
        message: h("i", { style: "color: teal" }, "收藏成功"),
        position: 'bottom-right'
      });
    },
    updateError() {
      const h = this.$createElement;
      this.$notify({
        title: "",
        message: h("i", { style: "color: rgb(168, 23, 23)" }, "收藏重复"),
        position: 'bottom-right'
      });
    },
    noLog() {
      const h = this.$createElement;
      this.$notify({
        title: "操作失败",
        message: h("i", { style: "color: rgb(168, 23, 23)" }, "请先登录")
      });
    }
  }
};
</script>

