import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userName: '',
    userConfirm: false,
    userProject: [],
    updateProject: false

  },
  getters: {
    getUserName(state) {
      return state.userName
    },
    getUserProject(state) {
      return state.userProject
    },
  },
  mutations: {
    updateUser(state, newUser) {
      state.userConfirm = true
      state.userName = newUser;
    },
    updateProject(state, newProject) {

      let result = state.userProject.find(function (item) {
        return item.name == newProject.name
      })
      if (result) {
        state.updateProject = false
      } else {
        state.updateProject = true
        state.userProject.push(newProject);
      }

    },
    deleteProject(state, oldProject) {
      let arr = state.userProject;
      arr.splice(arr.findIndex(item => item.name == oldProject), 1)
    }
  },
  actions: {

  },
  modules: {}
});
