<template>
  <div class="user">
    <div class="box" v-show="this.revisible">
      <div class="box-container">
        <div>
          <div class="quit" @click="quit">
            <i class="el-icon-close"></i>
          </div>
          <img src="../../assets/crocat.gif" />
          <div v-show="registerToggle">
            <p>期待您的加入！</p>
            <form @submit="signin">
              <label for="sUser">用户名</label>
              <el-input placeholder="请输入用户名" v-model="sUser" id="sUser"></el-input>
              <label for="sPassword">密码</label>
              <el-input placeholder="请输入密码" v-model="sPassword" id="sPassword" show-password></el-input>
              <!-- <label for="sEmail">邮箱</label> -->
              <!-- <el-input placeholder="请输入邮箱" v-model="sEmail" id="sEmail"></el-input> -->
              <el-button type="primary" @click="signin">注册</el-button>
            </form>已有账号？
            <span @click="toggle" class="toogle">登陆</span>
          </div>
          <div v-show="!registerToggle">
            <p>欢迎回来!</p>
            <form @submit="login">
              <label for="lUser">用户名</label>
              <el-input placeholder="请输入用户名" v-model="lUser" id="lUser"></el-input>
              <label for="lPassword">密码</label>
              <el-input placeholder="请输入密码" v-model="lPassword" id="lPassword" show-password></el-input>
              <el-button type="primary" @click="login">登陆</el-button>
            </form>还未注册？
            <span @click="toggle" class="toogle">加入</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import "../../initLeanCloud";
export default {
  props: {
    revisible: Boolean
  },
  data() {
    return {
      registerToggle: true,
      sUser: "",
      sPassword: "",
      // sEmail: "",
      lUser: "",
      lPassword: "",
      temporary: ""
    };
  },
  methods: {
    quit() {
      this.$emit("quit");
    },
    toggle() {
      this.registerToggle = !this.registerToggle;
    },
    reset() {
      (this.sUser = ""),
        (this.sPassword = ""),
        // (this.sEmail = ""),
        (this.lUser = ""),
        (this.lPassword = "");
    },
    signin(e) {
      var AV = require("leancloud-storage");
      var user = new AV.User();
      user.setUsername(this.sUser);
      user.setPassword(this.sPassword);
      // user.setEmail(this.sEmail);
      user.signUp().then(
        user => {
          console.log(`注册成功，key:${user.id}`);
          this.registerToggle = false;
          this.signSuccess();
        },
        error => {
          this.error(error);
        }
      );
      this.reset();
      e.preventDefault();
    },
    login() {
      this.temporary = this.lUser;
      var AV = require("leancloud-storage");
      AV.User.logIn(this.lUser, this.lPassword).then(
        user => {
          console.log(`登陆成功，key:${user.id}`);
          this.updateUser();
          this.logSuccess();
          this.quit();
        },
        error => {
          this.error(error);
        }
      );
      // this.info = this.lUser;

      this.reset();
    },
    updateUser() {
      this.$store.commit("updateUser", this.temporary);
    },
    signSuccess() {
      // this.$notify({
      //   title: "注册成功",
      //   message: "已自动跳转到登陆页面"
      // });
      const h = this.$createElement;
      this.$notify({
        title: "注册成功",
        message: h("i", { style: "color: teal" }, "已自动跳转到登陆页面")
      });
    },
    logSuccess() {
      // this.$notify({
      //   title: "登陆成功",
      //   message: "希望你在这度过一段愉快的时间"
      // });
      const h = this.$createElement;
      this.$notify({
        title: "登陆成功",
        message: h(
          "i",
          { style: "color: teal" },
          "tips:轻点喜欢的杂志即可添加到收藏夹"
        ),
        duration: 0
      });
    },
    error(e) {
      // this.$notify({
      //   title: "操作失败",
      //   message: e
      // });
      const h = this.$createElement;
      this.$notify({
        title: "操作失败",
        message: h("i", { style: "color: rgb(168, 23, 23)" }, "" + e)
      });
    }
  }
};
</script>

