import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import leanCloud from 'leancloud-storage'
import { Input, Button, Popover, Notification,DropdownItem,DropdownMenu,Dropdown } from 'element-ui'

Vue.use(Input)
Vue.use(Button)
Vue.use(Popover)
Vue.use(DropdownItem)
Vue.use(DropdownMenu)
Vue.use(Dropdown)
// Vue.use(MessageBox)


Vue.prototype.$notify = Notification;


Vue.use(leanCloud)
Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
